        return callback(new Error('先息后本，借款期限只能为1或3或6个月'));
      } else if(this.item.loan_type === '2' && [6,12,18,24].indexOf(value) === -1){
        return callback(new Error('等额等息，借款期限只能为6或12或18或24个月'));
