          {name: '自雇人士', value: 40},
          {name: '授薪人士（公司经理级以上管理人员）', value: 24},
          {name: '授薪人士（公司普通员工）', value: 12},
          {name: '无业人员', value: 4}
        ],
        income:[
          {name: '10000元（含）以内', value: 12},
          {name: '10000元-20000元（含）', value: 24},
          {name: '20000元-30000元（含）', value: 36},
          {name: '30000元-40000元（含）', value: 60},
          {name: '40000元-50000元（含）', value: 84},
          {name: '50000元以上', value: 120}
        ],
        loan_type:[
          {name: '全款车押证', value: 48},
          {name: '全款车押车', value: 80}
        ],
        car_policy:[
          {name: '全险（至少包括第三者责任、盗抢、车损及不计免赔）', value: 60},
          {name: '第三者责任、盗抢或车损、不计免赔', value: 24},
          {name: '第三者责任、不计免赔', value: 6}
        ],
        face_review:[
          {name: '非常好', value: 60},
          {name: '良好', value: 42},
          {name: '一般', value: 30},
          {name: '较差', value: 6}
        ]
      }
    }
  },
  computed:{
    info(){
      return this.item.wind_control;
    },
    total_score(){
      let total = 0;
      for(let key in this.item.score_card){
        if(key !== 'score' && key !== 'total_score'){
          total += this.item.score_card[key];
        }
      }
      return total;
    }
  },
  methods:{
    handlePreview(file){
      window.open(file.response);
    },
    handleExceed(file, fileList){
      this.$message.error('文件个数超出限制！');
    },
    handleScoreCardChange(file, fileList){
      this.item.complex_score_card = fileList;
    },
    handleSearchInfoChange(file, fileList){
      this.item.net_search_info = fileList;
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${ file.name }？`);
    },
    onCheckEvent(){
      this.$emit('detail');
    },
    closeEvent(){
      const that = this;
      this.visible = false;
      setTimeout(()=>{that.$emit('close');},500);
    },
    onPassEvent(){
      let data = ObjCopy(this.item);
      if(data.complex_score_card.length === 0){
        this.$message('请上传综合评分表！');
        return false;
      }else{
        data.complex_score_card = data.complex_score_card[0].response?data.complex_score_card[0].response:data.complex_score_card[0].url;
      }
      if(data.net_search_info.length === 0){
        this.$message('请上传网查资料压缩包！');
        return false;
      }else{
        data.net_search_info = data.net_search_info[0].response?data.net_search_info[0].response:data.net_search_info[0].url;
      }
      this.$refs['itemForm'].validate((valid) => {
        if(valid){
          this.$emit('pass', data);
        }else{
          this.$message('请正确填写表单！');
        }
      });
    }
  },
  mounted(){
    if(!this.target.wind_control.wind_control_id){
      this.item = {   
        "bill_id": this.target.bill_info.bill_id,                //订单id 
        "bill_status": 2,           //审核之后的订单状态 (驳回到之前的任意一个审核状态，或者下个状态)
        // "wind_control_id": 1,        //风控审核id(如果是更新的话就需要带上)
        // "money": 0,               //初审金额
        "loan_money": 0,           //放款金额
        "status": 1,                 //1通过 -1驳回
        "opinion": "无",         //审核意见
        "complex_score_card": [], //综合评分表 (上传文件)
        "net_search_info": [],    //网查资料压缩包
        "loan_product": null,           //借款产品
        "loan_type": null,              //借款类型
        "loan_start": "2018-01-01",  //开始
        "loan_end": "2018-01-01",    //结束
        "loan_time": 0,             //借款期限
        "contract_fee": 0,        //合同金额
        "interest": null,            //利息
        "credit_money": 0,      //信用贷金额
        "credit_interest": 0,     //信用贷利息
        "mortgage_registration": 0, //抵押登记费
        "parking_fee": 0,           //停车费
        "check_file_fee": 0,        //查档费用
        "study_fee": 0,             //考察费
        "guarantee": 0,             //保证金
        "gps_fee": 0,               //GPS拆装费
        "gps_server_fee": 0,       //GPS 信息服务费
        "procedure_fee": 0,
        "car_money": 0,
        "score_card": {
            "sex": 9,  //性别对应的分数
            "age": 21, //年龄对应分数
            "household" : 10,//对应分数
            "marrie_status": 10,//对应分数
            "education": 4,//对应分数
            "living_status": 20,//对应分数
            "family": 10,//对应分数
            "work": 4,//对应分数
            "income": 12,//对应分数
            "loan_type": 8,//对应分数
            "car_policy": 6,//对应分数
            "face_review": 30//对应分数
        }
      }
    }else{
      this.item = this.target.wind_control;
      if(this.item.net_search_info === ''){
        this.item.net_search_info = [];
      }else{
        this.item.net_search_info = [{name: '下载链接', url: this.item.net_search_info}]
      }
      if(this.item.complex_score_card === ''){
        this.item.complex_score_card = [];
      }else{
        this.item.complex_score_card = [{name: '查看', url: this.item.complex_score_card}]
      }
      this.item.bill_id = this.target.bill_info.bill_id;
      this.item.bill_status = 2;
    }
  }
}
</script>
<style scoped>
  .m-orderManage-auditDialog .detail-title {
    line-height: 24px;
    font-size: 16px;
    color: #2d2f33;
    border-bottom: 1px #ddd solid;
    margin-bottom: 10px;
    padding: 0 30px;
  }
  .m-orderManage-auditDialog .el-form {
    padding: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel{
    border: 2px #ddd dotted;
    margin-bottom: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel .title{
    padding: 20px;
    font-size: 16px;
    font-weight: bold;
  }
</style>
<style>
  .el-upload--text{
    width: auto;
    height: auto;
    border: none;
  }
</style>





